@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400&display=swap);
/* Home page style */

* {
  box-sizing: border-box;
}

h1, p, h2, h3, h4 {
  color: #1b3a4b;
}

.firstBlock {
  font-size: 1.5em;
}

.middleBlock {
  background-color: rgb(0, 100, 102, 0.4);
  height: 700px;
  font-size: 1.5em;
}

body {
  margin: 0;
  font-family: 'Quicksand', sans-serif;
}

.container {
  padding: 64px;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

.column-66 {
  float: left;
  width: 66.66666%;
  padding: 20px;
}

.column-33 {
  float: left;
  width: 33.33333%;
  padding: 20px;
}

.large-font {
  font-size: 48px;
}

.semi-large-font {
  font-size: 32px;
}

.qoute {
  font-size: 20px;
}

.xlarge-font {
  margin-top: 40px;
  font-size: 64px;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

@media screen and (max-width: 1000px) {
  .column-66, .column-33 {
    width: 100%;
    text-align: center;
  }
  img {
    margin: auto;
  }
}

/* end of styling home page */

